.basketPay {
    display: flex;
    justify-content: space-between;
}

.orderBuy {
    background-color: #000;
    border: 0.8px solid rgba(128, 128, 128, 0.143);
    transition: all 300ms ease-in;
    color: #fff;
    padding: 10px;
    border-radius: 10px;
    margin-bottom: 20px;
    width: 700px;
}

.map_delivery h2 {
    margin-bottom: 20px;
}

.orderBuy span {
    display: flex;
    align-items: center;
    gap: 20px;
    /* color: red; */
    font-weight: bold;
    padding: 5px;
}

.yMoneyForm {
    display: flex;
    flex-direction: column;
}

.send_value {}

.basketPay-block {
    /* color: #fff;
    background-color: #000;
    padding: 10px;
    margin-top: 20px;
    font-size: 20px;
    height: 100px;
    font-weight: bold;
    width: 330px;
    border-radius: 20px; */
    display: flex;
    /* justify-content: start; */
    align-items: center;
    justify-content: space-between;
    transition: all 0.4s ease-in-out;
    /* position: relative; */
    /* overflow: hidden; */
    flex-wrap: wrap;
    width: 700px;
}

.latitudeLongitude {
    display: flex;
    justify-content: space-around;
    width: 600px;
    margin-top: 20px;
}

.address_delivery {
    opacity: 0.7;
    font-size: 10px;
    padding-bottom: 6px;
    /* width: 100%; */
    width: 550px;
    height: 50px;
}

.latitudeLongitude div {
    font-weight: bold;
}

.orderBuy button {

    border: none;
    color: #fff;
    font-size: 18px;
    background-color: #000;
    padding: 5px;
    transition: all 300ms ease-in;
}

.orderBuy button:hover {
    scale: 1.04;
    /* border: 0.8px solid rgba(128, 128, 128, 0.743); */

    transition: all 300ms ease-in;
}


.order-item-test-buy {
    background-color: #fff;
    width: auto;
    color: #000;
    border-radius: 10px;
    padding: 5px;
    margin-bottom: 10px;
    font-size: 14px;
    font-weight: bold;
    /* color: red; */
}

.orderItem-name-buy {
    font-size: 16px;
    font-weight: 800;
}

.diamond-wrapper-order {
    position: absolute;
    width: 100%;
    opacity: 0.7;
    z-index: -1;
    top: -15%;
    left: 0;
    /* right: -45px; */
    /* bottom: -60px; */

}

.map_pay {
    width: 600px;
    height: 400px;
    border-radius: 20px;
    z-index: 1;
}


@media screen and (max-width: 1600px) {
    .orderBuy {
        width: 500px;
    }
}



.send_value {

    width: 150px;
    position: relative;
    margin-top: 5px;
    padding: 10px;
    border-radius: 6px;
    border: none;
    color: #fff;
    cursor: pointer;
    background-color: #7d2ae8;
    transition: all 0.2s ease;

}